* {
  font-family: Arial, Helvetica, sans-serif;
}
body {
  padding: 60px;
}
input {
  color: gray;
  font-size: 1.2rem;
  min-width: 350px;
}
.card-grid {
  display: grid;
  gap: 1rem;
  grid-auto-flow: dense;
  grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
}
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img {
  max-height: 170px;
}
